.card__description {
  margin: 0;
  color: var(--gray-color);
  flex: 1 0 auto;
  font-size: var(--font-size-main);
  line-height: var(--line-height-m);
}

@media screen and (max-width: 640px) {
  .card__description {
    font-size: 14px;
    line-height: 1.1;
    max-width: 245px;
  }
}