.scheme__img {
  max-width: 170px;
  width: 100%;
  aspect-ratio: 170 / 165;
  margin: 37px auto;
}

.scheme__img:last-of-type {
  margin: 30px auto;
}

@media screen and (max-width: 640px) {
  .scheme__img {
    margin: 0 auto;
  }

  .scheme__img:last-of-type {
    margin: 0 auto;
  }
}